<template>
    <div id="ToastPage" style="background-color:#eee">
      <vue-nav-bar
          title="Notification示例"
          left-text="返回"
          left-arrow
          @click-left="goBack"
      />
      <div style="margin-top:20px;">
        <vue-row>
          <vue-button style="width:100%" plain @click="openSuccessNotify">打开success通知类型</vue-button>
        </vue-row>
        <vue-row>
          <vue-button style="width:100%" plain @click="openWarningNotify">打开warning通知类型</vue-button>
        </vue-row>
        <vue-row>
          <vue-button style="width:100%" plain @click="openInfoNotify">打开info通知类型</vue-button>
        </vue-row>
        <vue-row>
          <vue-button style="width:100%" plain @click="openErrorNotify">打开error通知类型</vue-button>
        </vue-row>
        <vue-row>
          <vue-button style="width:100%" plain @click="openTitleNotify">打开标题通知</vue-button>
        </vue-row>
        <vue-row>
          <vue-button style="width:100%" plain @click="openLoadingNotify">打开Loading通知</vue-button>
        </vue-row>
        <vue-row>
          <vue-button style="width:100%" plain @click="openTextNotify">打开文本通知</vue-button>
        </vue-row>
        <vue-row>
          <vue-button style="width:100%" plain @click="openLongTextNotify">打开长文本自定义通知</vue-button>
        </vue-row>
    </div>
  </div>
</template>
<style>
  .notification-demo {
    color: #fff;
    font-size: 16px;
    background-color:rgba(0,0,0,0.8);
  }
</style>

<script>
export default {
  name: 'ToastPage',
  components: {
  },

  data() {
    return {
    };
  },
  methods: {
    goBack() {
      this.$router.go(-1);
    },
    openSuccessNotify() {
      this.$notify({
        title: 'Success',
        message: "I'm success notify",
        type: 'success',
        duration: 2000,
      });
    },
    openWarningNotify() {
      this.$notify({
        title: 'Warning',
        message: "I'm warning notify",
        type: 'warning',
        duration: 2000,
      });
    },
    openInfoNotify() {
      this.$notify({
        title: 'Info',
        message: 'I\'m info notify',
        type: 'info',
      });
    },
    openErrorNotify() {
      this.$notify({
        title: 'Error',
        message: "I'm error notify",
        type: 'error',
        position: 'top-right',
        duration: 0,
      });
    },
    openTitleNotify() {
      this.$notify({
        title: '文本标题',
        message: '文本内容',
        duration: 2000,
      });
    },
    openLoadingNotify() {
      this.$notify({
        message: 'Loading',
        iconClass: 'vue-icon-loading',
        duration: 2000,
      });
    },
    openTextNotify() {
      this.$notify({
        message: '文本轻提示',
        duration: 2000,
      });
    },
    openLongTextNotify() {
      this.$notify({
        message: '这是一条长文字的轻提示示例，超过一定的长度时，可以自动换行显示。如需修改请联系维护团队',
        customClass: 'notification-demo',
        duration: 3000,

      });
    },
  },
};
</script>
