      <div style="margin:6px;"> 选项分组用法</div>
      <vue-form-item label="分组选项">
        <vue-select
          v-model="mGroupItem"
          placeholder="请选择">
          <vue-option-group v-for="(group,index) in mGroupList" :key="index" :label="group.label">
