                      :label="item.label"
                      :value="item.value">
          </vue-option>
        </vue-select>
      </vue-form-item>
      <div style="margin:6px;"> 创建条目（用allow-create属性可通过在输入框中输入文字来创建新的条目.注意filterable必须为真）</div>
