      const self = this;
      if (query !== "") {
        this.loading = true;
        setTimeout(function() {
          self.loading = false;
          self.mRemoteOptions = self.mRemoteList.filter((item) =>{
            return item.label.toLowerCase().indexOf(query.toLowerCase()) > -1;
          });
        }, 300);
      } else {
        self.mRemoteOptions = [];
      }
    }
