<template>
  <div id="StepperPage" style="background-color:#eee">
    <vue-nav-bar
          title="步进器示例"
          left-text="返回"
          left-arrow
          @click-left="goBack"
    />
    <div class="div_content">
        <div class="div_item">
        <div style="padding:10px;">
            <span> 基础用法</span>
            <vue-stepper v-model="value1" />
        </div>
    </div>

   <div class="div_item">
        <div style="padding:10px;">
            <span> 步长设置（通过step属性设置每次点击增加或减少按钮时变化的值，默认值为1，间隔设置为3）</span>
            <vue-stepper v-model="value2" step="3" />
        </div>
    </div>

   <div class="div_item">
        <div style="padding:10px;">
            <span> 限制输入范围（通过min和max属性限制输入值的范围）</span>
            <vue-stepper v-model="value3" min="3" max="9"/>
        </div>
    </div>

   <div class="div_item">
        <div style="padding:10px;">
            <span> 限制输入整数（设置integer属性后，输入框将限制只能输入整数）</span>
            <vue-stepper v-model="value4" integer/>
        </div>
    </div>

   <div class="div_item">
        <div style="padding:10px;">
            <span> 禁用状态（通过设置disabled属性来禁用步进器，禁用状态下无法点击按钮或修改输入框）</span>
            <vue-stepper v-model="value4" disabled/>
        </div>
    </div>

   <div class="div_item">
        <div style="padding:10px;">
            <span> 禁用输入框（通过设置disable-input属性来禁用输入框，此时按钮仍然可以点击）</span>
            <vue-stepper v-model="value5" disable-input/>
        </div>
    </div>

   <div class="div_item">
        <div style="padding:10px;">
            <span> 固定小数位数（通过设置decimal-length属性可以保留固定的小数位数）</span>
            <vue-stepper v-model="value6" step="0.01" :decimal-length="2"/>
        </div>
    </div>

   <div class="div_item">
        <div style="padding:10px;">
            <span> 自定义大小（通过input-width属性设置输入框宽度，通过button-size属性设置按钮大小和输入框高度）</span>
            <vue-stepper v-model="value7" input-width="50px"  button-size="40px"/>
        </div>
    </div>

   <div class="div_item">
        <div style="padding:10px;">
            <span> 异步变更（如果需要异步地修改输入框的值，可以设置async-change属性，并在change事件中手动修改value）</span>
            <vue-stepper :value="value8" async-change @change="onChange" :disabled="isDisabled" />
        </div>
    </div>
    </div>
   
        
  </div>
</template>
<style>
    .div_content{
        overflow: auto;
        height: calc(100vh - 46px);
    }
    .div_item{
        margin-top:30px;
        border-bottom: 1px solid #fff;
    }
</style>
<script>
export default {
  name: 'StepperPage',
  components: {
  },

  data() {
    return {
        value1: 0,
        value2: 0,
        value3: 0,
        value4: 0,
        value5: 0,
        value6: 0,
        value7: 0,
        value8: 0,
        isDisabled:false
      
    };
  },
  methods: {
    goBack() {
      this.$router.go(-1);
    },
    onChange(value){
        // eslint-disable-next-line @typescript-eslint/no-this-alias
        const self = this;
        self.isDisabled = true;
        this.$notify({message: 'Loading',
        iconClass: 'vue-icon-loading',
        onClose: function() {
            self.value8 = value;
            self.isDisabled = false;
        }
        });
    }
    
  },
};
</script>
