<template>
  <div id="TabBarPage" style="background-color:#eee">
    <vue-nav-bar
          title="TabBar标签示例"
          left-text="返回"
          left-arrow
          @click-left="goBack"
    >
    </vue-nav-bar>
    <div class="div_content">
      <div style="margin-top:30px;">
      <div style="margin:6px;"> 基础用法</div>
      <vue-tabbar v-model="activeIndex" :fixed="isFixed">
          <vue-tabbar-item icon="home">首页</vue-tabbar-item>
          <vue-tabbar-item icon="search">搜索</vue-tabbar-item>
          <vue-tabbar-item icon="user">通讯录</vue-tabbar-item>
          <vue-tabbar-item icon="setting">设置</vue-tabbar-item>
      </vue-tabbar>
    </div>
    <div style="margin-top:30px;">
      <div style="margin:6px;"> 通过名称匹配</div>
      <vue-tabbar v-model="activeName" :fixed="isFixed">
          <vue-tabbar-item name="home" icon="home">首页</vue-tabbar-item>
          <vue-tabbar-item name="search" icon="search">搜索</vue-tabbar-item>
          <vue-tabbar-item name="user" icon="user">通讯录</vue-tabbar-item>
          <vue-tabbar-item name="setting" icon="setting">设置</vue-tabbar-item>
      </vue-tabbar>
    </div>
    <div style="margin-top:30px;">
      <div style="margin:6px;"> 微标提示</div>
      <vue-tabbar v-model="activeIndex2" :fixed="isFixed">
          <vue-tabbar-item icon="home" badge="8">首页</vue-tabbar-item>
          <vue-tabbar-item icon="search" dot>搜索</vue-tabbar-item>
          <vue-tabbar-item icon="user" badge="6">通讯录</vue-tabbar-item>
          <vue-tabbar-item icon="setting">设置</vue-tabbar-item>
      </vue-tabbar>
    </div>
    <div style="margin-top:30px;">
      <div style="margin:6px;"> 自定义图标（通过icon插槽自定义图标，可以通过 slot-scope 判断标签是否选中）</div>
      <vue-tabbar v-model="active" :fixed="isFixed">
        <vue-tabbar-item dot>
          <span>自定义</span>
          <template #icon="props">
            <img :src="props.active ? icon.active:icon.inactive"/>
          </template>
        </vue-tabbar-item>
        <vue-tabbar-item icon="search">搜索</vue-tabbar-item>
          <vue-tabbar-item icon="user" badge="6">通讯录</vue-tabbar-item>
          <vue-tabbar-item icon="setting">设置</vue-tabbar-item>
      </vue-tabbar>
    </div>
    <div style="margin-top:30px;">
      <div style="margin:6px;"> 自定义颜色+监听切换事件用法</div>
      <vue-tabbar v-model="activeIndex3" :fixed="isFixed" active-color="#07c160" inactive-color="lightblue" @change="onTabChange">
          <vue-tabbar-item v-for="item in mItemList" :key="item.iconName" :icon="item.iconName">{{item.tabLabel}}</vue-tabbar-item>
      </vue-tabbar>
    </div>

    <div style="margin-top:30px;">
      <div style="margin:6px;"> 路由模式（用于搭配vue-router使用。路由模式下会匹配页面路径和标签的to属性，并自动选中对应的标签）</div>
      <router-view />
      <vue-tabbar :fixed="isFixed" route>
          <vue-tabbar-item v-for="item in mItemList2" :key="item.iconName" replace :to="item.toUrl" :icon="item.iconName">{{item.tabLabel}}</vue-tabbar-item>
      </vue-tabbar>
    </div>
    </div>
    
  </div>
</template>
<style scoped>
  .div_content{
    overflow: auto;
    height: calc(100vh - 46px);

  }
</style>
<script>
export default {
  name: 'TabBarPage',
  components: {
  },

  data() {
    return {
      activeIndex:0,
      activeName: 'search',
      activeIndex2:2,
      activeIndex3:3,
      isFixed:false,
      active:0,
      icon: {
        active: 'https://img.yzcdn.cn/vant/user-active.png',
        inactive: 'https://img.yzcdn.cn/vant/user-inactive.png',
      },
      mItemList:[
        {
          iconName: 'home',
          tabLabel: '首页'
        },
        {
          iconName: 'search',
          tabLabel: '搜索'
        },
        {
          iconName: 'user',
          tabLabel: '通讯录'
        },
        {
          iconName: 'setting',
          tabLabel: '设置'
        },
      ],
      mItemList2:[
        {
          toUrl: '/form',
          iconName: 'home',
          tabLabel: '表单页'
        },
        {
          toUrl: '/select',
          iconName: 'menu',
          tabLabel: '选择页'
        },
        {
          toUrl: '/cascader',
          iconName: 'user',
          tabLabel: '级联页'
        },
        {
          toUrl: '/input',
          iconName: 'setting',
          tabLabel: '输入页'
        },
      ]
    };
  },
  methods: {
    goBack() {
      this.$router.go(-1);
    },
    onTabChange(index) {
      const item = this.mItemList[index];
      this.$notify({message:'当前点击了'+item.tabLabel+'标签项'});
    }
  },
};
</script>
