          left-arrow
          @click-left="goBack"
        />
        <div class="div_content">
          <div style="margin:30px 0;">
            <div style="margin: 6px;font-weight:bold;">基础用法(设置文字水平居中)</div>
            <vue-input v-model="inputValue1" :placeholder="hinitMsg" text-align="center" ></vue-input>
          </div>
          <div style="margin:30px 0;">
            <div style="margin: 6px;font-weight:bold;">启用限制数字输入</div>
            <vue-input v-model="inputValue2" :placeholder="hinitMsg" :cleave="customInputData1" ></vue-input>
          </div>
          <div style="margin:30px 0;">
            <div style="margin: 6px;font-weight:bold;">仅数字输入</div>
            <vue-input v-model="inputValue2" :placeholder="hinitMsg" :cleave="customInputData2" ></vue-input>
          </div>
          <div style="margin-top:20px;">
            <div style="margin: 6px;font-weight:bold">自定义格式输入框(更多用法查看Cleave Options)
            <li style="font-weight:normal">根据数组值[3,3,3]来分隔输入字符</li>
            <li style="font-weight:normal">设置分隔符为"-"</li>
            <li style="font-weight:normal">自动转换为大写字母</li>
          </div>
          <vue-input v-model="inputValue3" :placeholder="hinitMsg" :cleave="customInputData3">
          </vue-input>
        </div>
        <div style="margin-top:20px;">
          <div style="margin: 6px;font-weight:bold">禁用状态</div>
          <vue-input :placeholder="hinitMsg2" :disabled="true"></vue-input>
        </div>
        <div style="margin-top:20px;">
          <div style="margin: 6px;font-weight:bold">右对齐的带icon输入框</div>
          <vue-input v-model="inputValue4"
                     :placeholder="hinitMsg"
                     icon="vue-icon-search"
                     :on-icon-click="clickIconEvent">
          </vue-input>
        </div>
        <div style="margin-top:20px;">
          <div style="margin: 6px;font-weight:bold">mini尺寸的左对齐的带icon输入框</div>
          <vue-input v-model="inputValue5"
                     :placeholder="hinitMsg"
                     size="mini"
                     prefixIcon="vue-icon-user"
                     :on-icon-click="clickIconEvent">
          </vue-input>
        </div>
        <div style="margin-top:20px;">
          <div style="margin: 6px;font-weight:bold">small尺寸的左对齐的带icon输入框</div>
          <vue-input v-model="inputValue6"
                     :placeholder="hinitMsg"
                     size="small"
                     prefixIcon="vue-icon-user"
                     :on-icon-click="clickIconEvent">
          </vue-input>
