                     :placeholder="hinitMsg"
                     prefixIcon="vue-icon-user"
                     :on-icon-click="clickIconEvent">
          </vue-input>
        </div>
        <div style="margin-top:20px;">
          <div style="margin: 6px;font-weight:bold">large尺寸的左对齐的带icon输入框</div>
          <vue-input v-model="inputValue8"
                     :placeholder="hinitMsg"
                     size="large"
                     prefixIcon="vue-icon-user"
                     :on-icon-click="clickIconEvent">
          </vue-input>
        </div>
        <div style="margin-top:20px;">
          <div style="margin: 6px;font-weight:bold">带建议的输入框示例</div>
          <li style="margin: 6px;font-weight:normal">获取焦点时即弹出输入建议</li>
          <vue-autocomplete v-model="state1"
                     :placeholder="hinitMsg"
                     :fetch-suggestions="querySearch"
                     @select="handleSelect">
          </vue-autocomplete>
           <li style="margin: 6px;font-weight:normal">输入内容经匹配后才弹出输入建议</li>
          <vue-autocomplete v-model="state2"
                     :placeholder="hinitMsg"
                     :fetch-suggestions="querySearch"
                     @select="handleSelect"
                     :trigger-on-focus="false">
          </vue-autocomplete>
        </div>
        <div style="margin-top:20px;">
          <div style="margin: 6px;font-weight:bold">文本域</div>
          <vue-input v-model="inputValue3"
                     :placeholder="hinitMsg"
                     type="textarea"
                     :autosize="{ minRows: 2, maxRows: 4}">
          </vue-input>
        </div>

        <div style="margin:30px 0;">
          <div style="margin: 6px;font-weight:bold;">TEL键盘类型</div>
          <vue-input keyBoardType="tel" v-model="inputValue10" :placeholder="hinitMsg" text-align="center" ></vue-input>
        </div>
                <div style="margin:30px 0;">
          <div style="margin: 6px;font-weight:bold;">NUMBER键盘类型</div>
          <vue-input keyBoardType="number" v-model="inputValue11" :placeholder="hinitMsg" text-align="center" ></vue-input>
        </div>
        <div style="margin:30px 0;">
          <div style="margin: 6px;font-weight:bold;">ONLYNUMBER键盘类型</div>
          <vue-input keyBoardType="onlynumber" v-model="inputValue12" :placeholder="hinitMsg" text-align="center" ></vue-input>
        </div>
        <div style="margin:30px 0;">
          <div style="margin: 6px;font-weight:bold;">PASSWORD键盘类型</div>
          <vue-input keyBoardType="password" v-model="inputValue13" :placeholder="hinitMsg" text-align="center" ></vue-input>
        </div>
        <div style="margin:30px 0;">
          <div style="margin: 6px;font-weight:bold;">URL键盘类型</div>
          <vue-input keyBoardType="url" v-model="inputValue14" :placeholder="hinitMsg" text-align="center" ></vue-input>
        </div>
        <div style="margin:30px 0;">
          <div style="margin: 6px;font-weight:bold;">EMAIL键盘类型</div>
          <vue-input keyBoardType="email" v-model="inputValue15" :placeholder="hinitMsg" text-align="center" ></vue-input>
        </div>
      </div>
    </div>
</template>
<style scoped>
  .div_content{
    overflow: auto;
    height: calc(100vh - 46px);
  }
</style>

<script>
export default {
  name: 'InputPage',
  data() {
    return {
      inputValue1: '',
      inputValue2: '',
      inputValue3: '',
      inputValue4: '',
      inputValue5: '',
      inputValue6: '',
      inputValue7: '',
      inputValue8: '',

      inputValue10: '',
      inputValue11: '',
      inputValue12: '',
      inputValue13: '',
      inputValue14: '',
      inputValue15: '',
      hinitMsg: '请输入内容',
      customInputData1: {
        numeral:true
      },
      customInputData2: {
        numericOnly:true
      },
      customInputData3: {
        delimiter: '-',//分隔符
        blocks: [3, 3, 3],
        uppercase: true,
      },
      hinitMsg2: '当前输入框已禁用',
      state1: '',
      state2: '',
      restaurants: [
        { value: 'first' },
        { value: 'second' },
        { value: 'first and second' },
      ],
    };
  },
  methods: {
    goBack() {
      this.$router.go(-1);
    },
    clickIconEvent() {
      this.$notify({ message: '点击了当前图标' });
    },
    querySearch(queryString, cb) {
      const { restaurants } = this;
      const results = queryString
        ? restaurants.filter(this.createFilter(queryString)) : restaurants;
      cb(results);
    },
    createFilter(queryString) {
      return (restaurant) => restaurant.value.indexOf(queryString.toLowerCase()) === 0;
    },
    handleSelect(item) {
      this.$notify({ title: item.value });
    },
  },
};
</script>
